<template>
  <v-navigation-drawer
    v-model=showDrawer
    app
    color=secondary
    :expand-on-hover=!mobile
    :mobile-breakpoint=$vuetify.breakpoint.thresholds.sm
    :mini-variant.sync=mini
    dark
  >
    <v-list shaped :dense=mobile>
      <v-list-item class=px-2>
        <v-list-item-avatar color="primary">
          JF
        </v-list-item-avatar>
      </v-list-item>

      <v-list-item class="navigationName">
        <v-list-item-content :class="mini ? 'transparent--text' : 'white--text'">
          <span class=title>{{ contact.shortName }}</span>
          <span class=subtitle>{{ contact.email }}</span>
        </v-list-item-content>
      </v-list-item>

      <v-divider />
      <v-list-item-group v-model="current">
        <template v-for="item in items">
          <v-list-group
            v-if=item.children
            :key=item.text
            v-model=item.model
            :prepend-icon="item.model ? item.icon : item.iconAlt"
            append-icon=""
          >
            <template v-slot:activator>
              <v-list-item-content>
                <v-list-item-title class=text-capitalize>
                  {{ item.text }}
                </v-list-item-title>
              </v-list-item-content>
            </template>
            <v-list-item
              v-for="(child, i) in item.children"
              :key=i
              link
              :href="child.url || undefined"
              :target="child.url || undefined"
            >
              <v-list-item-action v-if="child.icon">
                <v-icon v-text="child.icon" />
              </v-list-item-action>
              <v-list-item-content>
                <v-list-item-title class="text-capitalize">
                  {{ child.text }}
                </v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </v-list-group>

          <v-list-item
            v-else
            :key=item.text
            @click="$emit('select', item.name)"
          >
            <v-list-item-action>
              <v-icon v-text=item.icon />
            </v-list-item-action>
            <v-list-item-content>
              <v-list-item-title class="text-capitalize">
                {{ item.text }}
              </v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </template>
      </v-list-item-group>
    </v-list>
  </v-navigation-drawer>
</template>

<script>
export default {
  name: 'cvNavigation',
  model: {
    prop: 'drawer',
    event: 'toggleDrawer',
  },
  props: {
    contact: {
      type: Object,
      default: () => {},
    },
    drawer: {
      type: Boolean,
      default: true,
    },
    items: {
      type: Array[Object],
      default: () => [],
    },
    picture: {
      type: String,
      default: null,
    },
  },
  data: () => ({
    current: 0,
    mini: true,
  }),
  computed: {
    showDrawer: {
      get() {
        return this.drawer;
      },
      set(value) {
        this.$emit('toggleDrawer', value);
      },
    },
  },
  mounted() {
    this.mini = !this.mobile;
  },
};
</script>

<style scoped>
.navigationName {
  min-height: 10vh;
  display: inline-flex;
}
.v-timeline-item__divider {
  min-width: 5vw;
}
</style>
